.container-modal{
  position: absolute;
  background-color: rgba(0, 0, 0, 0.1);
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 1s;
  opacity: 1;
  animation: animationForModal 300ms linear;
}
@keyframes animationForModal {
 from{
  opacity: 0;
 }to{
    opacity: 1;
 }
}
.container-modal .modal{
